import * as PIXI from "pixi.js";

export class NextIssueInfo extends PIXI.Container {

    init() {
        this.info = new PIXI.Text(" 00:00:00\n 00:00:00", {
            fontFamily: 'Arial',
            fontSize: 20,
            fill: ['#ffffff'],
        });
        this.info.anchor.set(0.5);
        this.addChild(this.info);
    }

    upInfo(data) {
        this.info.text = ` ${data.nextTime}\n ${data.currTime}`;
    }
}